<template>
  <section>
    <!---->
    <el-col :span="12" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.query" placeholder=" all" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getRows" icon="el-icon-search"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="handleAdd" icon="el-icon-plus"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-table :data="rows" style="width: 100%" stripe border>
      <el-table-column label="" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.date }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="180" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>: {{ scope.row.name }}</p>
            <p>: {{ scope.row.address }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.name }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="sex" label="" width="100" align="center" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          {{scope.row.sex===1?'':''}}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </section>
</template>

<script>
  let data = () => {
    return {
      filters: {},
      rows: []
    }
  }

  let handleAdd = function() {

  }

  let handleEdit = function(index, row) {
    console.log(index, row);
  }

  let handleDelete = function(index, row) {
    console.log(index, row);
  }

  let getRows = function() {
    this.rows = []
    this.rows.push({
      date: '2018-05-02',
      name: '1',
      sex: 1,
      address: ' 1518 '
    })
    this.rows.push({
      date: '2018-05-04',
      name: '2',
      sex: 2,
      address: ' 1517 '
    })
    this.rows.push({
      date: '2018-05-01',
      name: '3',
      sex: 2,
      address: ' 1519 '
    })
    this.rows.push({
      date: '2018-05-03',
      name: '5',
      sex: 1,
      address: ' 1516 '
    })
  }

  export default {
    data: data,
    methods: {
      //
      handleAdd,
      //
      handleEdit,
      //
      handleDelete,
      //
      getRows
    },
    mounted: function() {
      this.getRows()
    }
  }
</script>

<style scoped>
</style>




